import { configureStore } from '@reduxjs/toolkit';
import sliceproduct from './sliceproduct';
import cartslice from './cartslice'; // تأكد من استيراد cartslice

export const store = configureStore({
  reducer: {
    products: sliceproduct,
    cartslice: cartslice, // أضف cartslice هنا
  },
});
